import { createElement } from '../render';
import './abstract-view.css';
/**
 *   
 */
export default class AbstractView {
    /**   */
    #element = null;
    constructor() {
        if (new.target === AbstractView) {
            throw new Error('Can\'t instantiate AbstractView, only concrete one.');
        }
    }
    /**
     *    
     *  
     */
    get element() {
        if (!this.#element) {
            this.#element = createElement(this.template);
        }
        return this.#element;
    }
    /**
     *     
     * @abstract
     * @returns     
     */
    get template() {
        throw new Error('Abstract method not implemented: get template');
    }
    /**     */
    removeElement() {
        this.#element = null;
    }
    /**
     * ,   " "
     * @param [callback] ,      
     */
    shake(callback) {
        this.element.classList.add("shake" /* SnakeAnimation.CLASS_NAME */);
        setTimeout(() => {
            this.element.classList.remove("shake" /* SnakeAnimation.CLASS_NAME */);
            callback?.();
        }, 600 /* SnakeAnimation.TIMEOUT */);
    }
}
