import { TYPES } from '../const';
import AbstractStatefulView from '../framework/view/abstract-stateful-view';
import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
// const enum FormName {
//   PRICE = 'event-price'
// }
function createDestinationOption({ name }) {
    /*html*/ return `<option value="${name}"></option>`;
}
function createEventTypeItem(type, point) {
    return /*html*/ `<div class="event__type-item">
<input id="event-type-${type}" class="event__type-input  visually-hidden" type="radio" name="event-type" value="${type}" ${point.type === type ? 'checked' : ''}>
<label class="event__type-label  event__type-label--${type}" for="event-type-${type}">${type}</label>
</div>`;
}
function createEventOffersSection(offers, point) {
    if (offers.length === 0) {
        return '';
    }
    const isChecked = (id) => point.offers.includes(id) ? 'checked' : '';
    return `<section class="event__section  event__section--offers">
  <h3 class="event__section-title  event__section-title--offers">Offers</h3>

  <div class="event__available-offers">
    ${offers.map(({ id, title, price }) => /*html*/ `<div class="event__offer-selector">
    <input class="event__offer-checkbox  visually-hidden" id="event-offer-${id}" data-offer-id="${id}" type="checkbox" name="event-offer-${point.type}" ${isChecked(id)}>
    <label class="event__offer-label" for="event-offer-${id}">
      <span class="event__offer-title">${title}</span>
      &plus;&euro;&nbsp;
      <span class="event__offer-price">${price}</span>
    </label></div>`).join('')}
  </div>
</section>`;
}
function createDestinationDescription(description, name) {
    return (description.length > 0 ? `<p class="event__destination-description">${name} - ${description}</p>` : '');
}
function createDestinationPhotos(pictures) {
    return (pictures.length > 0 ?
        `<div class="event__photos-container">
    <div class="event__photos-tape">
    ${pictures.map(({ src, description }) => /*html*/ `<img class="event__photo" src="${src}" alt="${description}">`).join('')}
    </div>
    </div>` : '');
}
function createEventDestinationSection(destination) {
    const { description, name, pictures } = destination;
    return ((description.length > 0 || pictures.length > 0) ?
        /*html*/ `<section class="event__section  event__section--destination">
  <h3 class="event__section-title  event__section-title--destination">Destination</h3>
  ${createDestinationDescription(description, name)}
  ${createDestinationPhotos(pictures)}
  </section>` : '');
}
function createTemplate({ point, destinations, destination, getOffersByType }) {
    const { price, dateFrom, dateTo } = point;
    const currentOffers = getOffersByType(point.type);
    return /*html*/ `<li class="trip-events__item">
  <form class="event event--edit" action="#" method="post">
  <header class="event__header">
  <div class="event__type-wrapper">
    <label class="event__type  event__type-btn" for="event-type-toggle-1">
      <span class="visually-hidden">Choose event type</span>
      <img class="event__type-icon" width="17" height="17" src="img/icons/${point.type}.png" alt="Event type icon">
    </label>
    <input class="event__type-toggle  visually-hidden" id="event-type-toggle-1" type="checkbox">

    <div class="event__type-list">
      <fieldset class="event__type-group">
        <legend class="visually-hidden">Event type</legend>
        ${TYPES.map((type) => createEventTypeItem(type, point)).join('')}
      </fieldset>
    </div>
  </div>

  <div class="event__field-group  event__field-group--destination">
    <label class="event__label  event__type-output" for="event-destination-1">
    ${point.type}
    </label>
    <input class="event__input  event__input--destination" id="event-destination-1" type="text" name="event-destination" value="${destination.name}" list="destination-list-1">
    <datalist id="destination-list-1">
      ${destinations.map(createDestinationOption).join('')}
    </datalist>
  </div>

  <div class="event__field-group  event__field-group--time">
    <label class="visually-hidden" for="event-start-time-1">From</label>
    <input class="event__input  event__input--time" id="event-start-time-1" type="text" name="event-start-time" value="${dateFrom}">
    &mdash;
    <label class="visually-hidden" for="event-end-time-1">To</label>
    <input class="event__input  event__input--time" id="event-end-time-1" type="text" name="event-end-time" value="${dateTo}">
  </div>

  <div class="event__field-group  event__field-group--price">
    <label class="event__label" for="event-price-1">
      <span class="visually-hidden">Price</span>
      &euro;
    </label>
    <input class="event__input  event__input--price" id="event-price-1" type="text" name="event-price" value="${price}">
  </div>

  <button class="event__save-btn  btn  btn--blue" type="submit">Save</button>
  <button class="event__reset-btn" type="reset">Cancel</button>
  <button class="event__rollup-btn" type="button">
  <span class="visually-hidden">Open event</span>
  </button>
  </header>
  <section class="event__details">
  ${createEventOffersSection(currentOffers, point)}
  ${createEventDestinationSection(destination)}
  </section>
  </form>
  </li>`;
}
// interface MainForm extends HTMLFormElement {
//   [FormName.PRICE]: HTMLInputElement;
// }
export default class FormView extends AbstractStatefulView {
    #destinations;
    #onRollupClick;
    #onFormSubmit;
    #getOffersByType;
    #getDestinationByCity;
    // #onFormReset: () => void;
    #datePickerFrom = null;
    #datePickerTo = null;
    constructor({ point, destinations, destination, onRollupClick, onFormSubmit, getOffersByType, getDestinationByCity }) {
        super();
        this._setState(FormView.parsePointToState(point, destination));
        this.#destinations = destinations;
        this.#onRollupClick = onRollupClick;
        this.#onFormSubmit = onFormSubmit;
        this.#getOffersByType = getOffersByType;
        this.#getDestinationByCity = getDestinationByCity;
        // this.#onFormReset = onFormReset;
        this._restoreHandlers();
    }
    _restoreHandlers = () => {
        const form = this.element.querySelector('form');
        form.addEventListener('submit', this.#formSubmitHandler);
        // form.addEventListener('reset', this.#onFormReset);
        this.element.querySelector('.event__rollup-btn')
            .addEventListener('click', this.#buttonRollupHandler);
        this.element.querySelector('.event__type-group')
            .addEventListener('change', this.#routeTypeHandler);
        this.element.querySelector('.event__input--destination')
            .addEventListener('change', this.#citySelectionHandler);
        const availableOffersElement = this.element.querySelector('.event__available-offers');
        if (availableOffersElement) {
            availableOffersElement.addEventListener('click', this.#offerClickHandler);
        }
        this.element.querySelector('.event__input--price')
            .addEventListener('change', this.#priceInputChange);
        this.#setDatePicker();
    };
    get template() {
        const { point, destination } = this._state;
        return createTemplate({
            point,
            destination,
            destinations: this.#destinations,
            getOffersByType: this.#getOffersByType,
            getDestinationByCity: this.#getDestinationByCity
        });
    }
    removeElement() {
        super.removeElement();
        if (this.#datePickerFrom) {
            this.#datePickerFrom.destroy();
            this.#datePickerFrom = null;
        }
        if (this.#datePickerTo) {
            this.#datePickerTo.destroy();
            this.#datePickerTo = null;
        }
    }
    reset(point, destination) {
        this.updateElement(FormView.parsePointToState(point, destination));
    }
    #formSubmitHandler = (evt) => {
        evt.preventDefault();
        // const form = evt.target as MainForm;
        this.#onFormSubmit(FormView.parseStateToPoint(this._state));
    };
    #buttonRollupHandler = (evt) => {
        evt.preventDefault();
        this.#onRollupClick();
    };
    #routeTypeHandler = (evt) => {
        evt.preventDefault();
        this.updateElement({
            point: {
                ...this._state.point,
                type: evt.target.value,
                offers: []
            }
        });
    };
    #citySelectionHandler = (evt) => {
        const city = evt.target.value;
        if (city === this._state.destination.name || city === '') {
            return;
        }
        const destination = this.#getDestinationByCity(city);
        if (destination) {
            this.updateElement({
                point: {
                    ...this._state.point,
                    destination: destination.id
                },
                destination
            });
        }
    };
    #offerClickHandler = () => {
        const checkedOffers = Array.from(this.element.querySelectorAll('.event__offer-checkbox:checked'));
        const offersIds = checkedOffers.map((offer) => offer.dataset.offerId);
        this._setState({
            point: {
                ...this._state.point,
                offers: offersIds
            }
        });
    };
    #priceInputChange = (evt) => {
        evt.preventDefault();
        this._setState({
            point: {
                ...this._state.point,
                price: evt.target.valueAsNumber
            }
        });
    };
    #dateFromChangeHandler = ([userDate]) => {
        this.updateElement({
            point: {
                ...this._state.point,
                dateFrom: userDate,
            }
        });
    };
    #dateToChangeHandler = ([userDate]) => {
        this.updateElement({
            point: {
                ...this._state.point,
                dateTo: userDate,
            }
        });
    };
    #setDatePicker() {
        const [dateFrom, dateTo] = this.element.querySelectorAll('.event__input--time');
        this.#datePickerFrom = flatpickr(dateFrom, {
            dateFormat: 'd/m/y H:i',
            defaultDate: this._state.point.dateFrom,
            maxDate: this._state.point.dateTo,
            enableTime: true,
            'time_24hr': true,
            'locale': {
                'firstDayOfWeek': 1 // start week on Monday
            },
            onChange: this.#dateFromChangeHandler
        });
        this.#datePickerTo = flatpickr(dateTo, {
            dateFormat: 'd/m/y H:i',
            defaultDate: this._state.point.dateTo,
            minDate: this._state.point.dateFrom,
            enableTime: true,
            'time_24hr': true,
            'locale': {
                'firstDayOfWeek': 1 // start week on Monday
            },
            onChange: this.#dateToChangeHandler
        });
    }
    static parsePointToState(point, destination) {
        return {
            point: { ...point },
            destination
        };
    }
    static parseStateToPoint(state) {
        return {
            ...state.point,
        };
    }
}
